html, body {
  max-width: 100vw;
  min-height: 100%;
  overflow-x: hidden; }

body {
  max-width: 100vw;
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #000;
  background-color: #fff;
  overflow-x: hidden; }

*,
*::before,
*::after {
  box-sizing: border-box; }

a {
  text-decoration: none; }

.header__top-block-container {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1800px;
  margin: 0 auto; }
